var ForEach = require('lodash.foreach');
var self = this;

this.browser = {
  Android: function() {
    return navigator.userAgent.match(/Android/i) ? true : false;
  },
  BlackBerry: function() {
    return navigator.userAgent.match(/BlackBerry/i) ? true : false;
  },
  iOS: function() {
    return navigator.userAgent.match(/iPhone|iPad|iPod/i) ? true : false;
  },
  OperaMini: function() {
    return navigator.userAgent.match(/Opera Mini/i) ? true : false;
  },
  WindowsPnone: function() {
    return (navigator.userAgent.match(/IEMobile/i) || navigator.userAgent.match(/WPDesktop/i)) ? true : false;
  },
  IE: function() {
    return navigator.userAgent.match(/MSIE/i) ? true : false;
  },
  Mac: function() {
    return navigator.userAgent.match(/Mac/i) ? true : false;
  },
  Wechat: function() {
    return navigator.userAgent.toLowerCase().match(/micromessenger/i) ? true : false;
  },
  Mobile: function() {
    return (self.browser.Android() || self.browser.BlackBerry() || self.browser.iOS() || self.browser.OperaMini() || self.browser.WindowsPnone()) ? true : false;
  }
};

this.decodeEmoji = function(_txt) {

  var txtList = _txt.replace(/\&\#.[0-9]+\;/g, "ysc1217|$|").split('ysc1217');
  var emojiList = _txt.match(/&#.\d*\;/g);

  var txt = "";

  if (emojiList != null) {
    var n = 0;
    ForEach(txtList, function(v, k) {
      if (v == "|$|") {
        var emoji = findSurrogatePair(emojiList[n]);
        txt += emoji;
        n++;
      } else if (v.indexOf("$|") != -1) {
        var emoji = findSurrogatePair(emojiList[n]);
        txt += emoji;
        n++;
        txt += v.replace("|$|", "");
      } else if (v.indexOf("|$|") == -1) {
        txt += v;
      }
    });

    if (n < emojiList.length - 1) {
      for (var i = n; i < emojiList.length; i++) {
        var emoji = findSurrogatePair(emojiList[i]);
        txt += emoji;
      }
    }
  } else {
    txt = _txt;
  }

  return txt;
};


function findSurrogatePair(point) {
  point = parseInt(point.replace("&#", "").replace(";", ""));
  var offset = point - 0x10000,
    lead = 0xd800 + (offset >> 10),
    trail = 0xdc00 + (offset & 0x3ff);
  return String.fromCharCode("0x" + lead.toString(16), "0x" + trail.toString(16));
}

this.getUrlParam = function(_name) {
  var reg = new RegExp("(^|&)" + _name + "=([^&]*)(&|$)");
  var r = window.location.search.substr(1).match(reg);
  if (r != null && r[2] != "") return decodeURIComponent(r[2]);
  return null
};

this.setUrlParam = function(_name, _val) {
  var t = document.title;
  var u;
  if (_val == '') {
    self.delUrlParam(_name);
  } else if (self.getUrlParam(_name) == null) {
    u = self.addUrlParam(_name, _val);
  } else {
    u = self.changeUrlParam(_name, _val);
  }
  var state = {
    title: t,
    url: u
  }
  window.history.replaceState(state, t, u);
};

this.delUrlParam = function(_name) {
  var reg = new RegExp("(^|&)" + _name + "=([^&]*)(&|$)");
  var url = window.location.href;
  var params = window.location.search.substr(1);
  url = url.substr(0, url.lastIndexOf('?'));
  var p = params.split(reg);
  var ps = p[0];
  if (p.length > 1) {
    if (ps != "" && p[4] != "") {
      ps += "&";
    }
    ps += p[4];
  }
  if (ps != "") {
    url += "?"
  }

  var t = document.title;
  var u = url + ps;
  var state = {
    title: t,
    url: u
  }
  window.history.replaceState(state, t, u);
};

this.addUrlParam = function(_name, _val) {
  var reg = new RegExp("(^|&)" + _name + "=([^&]*)(&|$)");
  var url = window.location.href;
  var ps = window.location.search.substr(1);
  if (ps != "") {
    url += "&" + _name + "=" + _val;
  } else {
    url += "?" + _name + "=" + _val;
  }
  return url;
};

this.changeUrlParam = function(_name, _val) {
  var reg = new RegExp("(^|&)" + _name + "=([^&]*)(&|$)");
  var url = window.location.href;
  var params = window.location.search.substr(1);
  url = url.substr(0, url.lastIndexOf('?'));
  var p = params.split(reg);
  if (p.length > 1) {
    p[2] = _name + "=" + _val;
  }
  var ps = "";
  forEach(p, function(val, index) {
    ps += val;
  });
  if (ps != "") {
    url += "?"
  }
  return url + ps;
};

Date.prototype.format = function(fmt) {
  var o = {
    "M+": this.getMonth() + 1,
    "d+": this.getDate(),
    "h+": this.getHours(),
    "m+": this.getMinutes(),
    "s+": this.getSeconds(),
    "q+": Math.floor((this.getMonth() + 3) / 3),
    "S": this.getMilliseconds()
  };
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
  }
  for (var k in o) {
    if (new RegExp("(" + k + ")").test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    }
  }
  return fmt;
}
