'use strict';

var Vue = require('vue');
var MuseUI = require('muse-ui');
Vue.use(MuseUI);

var axios = require('axios');
var _ = require('lodash');


// var AdMasterAPI = require('../../../AdmasterAPI.js');

// var $ = require('jquery');
// var TweenMax = require('gsap');
// var ForEach = require('lodash.foreach');
// var Random = require('lodash.random');
// var Swiper = require('swiper');


var Public = require('./Public.js');


Vue.config.productionTip = false;


var self = this;
var site;

var BASE_URL = "http://localhost:3000/"



init();

function init() {
  site = new Vue({
    el: '#wrapper',
    data: {
      isReady: false,
      showLoading:true,
      noData:false,
      platforms: [],
      tableHead: [],
      tableData: [],
      maxDate: new Date(new Date() - 24 * 60 * 60 * 1000).format("yyyy-MM-dd"),
      platform: '',
      startDate: '',
      endDate: '',
      maxResults: 50,
      platformErrorText: "",
      startDateErrorText: "",
      endDateErrorText: "",
      maxResultsErrorText: ""
    },
    methods: {
      clickBtn: function() {
        var f = true;
        if (this.platform === "") {
          this.platformErrorText = "must";
          f = false;
        } else {
          this.platformErrorText = ""
        }
        if (this.startDate === "") {
          this.startDateErrorText = "must";
          f = false;
        } else {
          this.startDateErrorText = ""
        }
        if (this.endDate === "") {
          this.endDateErrorText = "must";
          f = false;
        } else {
          this.endDateErrorText = ""
        }
        if (this.maxResults === "") {
          this.maxResultsErrorText = "must";
          f = false;
        } else if(this.maxResults>100){
          this.maxResultsErrorText = "The max results may not be greater than 100.";
          f = false;
        }else{
          this.maxResultsErrorText = ""
        }

        if (f) {
          site.tableData = [];
          site.showLoading = true;
          site.noData = false;

          axios.get('/adMaster/getData', {params:{
              "platforms": this.platforms[this.platform],
              "startDate": this.startDate + " 00:00:00",
              "endDate": this.endDate + " 23:59:59",
              "maxResults": this.maxResults
            }})
            .then(function(response) {
                site.showLoading = false;
              if(response.data.status==1)
              {
                if(response.data.data.length>0)
                {
                  site.tableHead = response.data.headers;
                  response.data.data = _.map(response.data.data,function(v){
                     return _.drop(v);
                   });
                   site.tableData = response.data.data;
                }else{
                  site.noData = true;
                }
              }
            });
        }
      }
    }
  });

  axios.get('./adMaster')
    .then(function(response) {
      site.platforms = response.data.data.platform;
      site.isReady = true;
      site.showLoading = false;
    });
};
